    .slider-container {
    max-width: 1400px;
    }

    .slider-image {
    width: 100%;
    height: 780px;
    border-radius: 2rem; 
    background-position: center;
    background-size: cover;
    transition-duration: 0.5s; 
    }


    .nav-arrow {
    display: none;
    position: absolute;
    top: 50%;
    transform: translateX(0) translateY(-50%);
    font-size: 2rem; 
    border-radius: 9999px; 
    padding: 0.5rem; 
    background-color: rgba(0, 0, 0, 0.2); 
    color: white; 
    cursor: pointer;
    }

    .nav-arrow.prev {
    left: 5px;
    }

    .nav-arrow.next {
    right: 5px;
    }

    .slider-container:hover .nav-arrow {
    display: block;
    }

    .slider-dots {
    display: flex;
    position: absolute;
    top: 4px;
    justify-content: center;
    }

    .dot {
    font-size: 2rem; 
    cursor: pointer; 
    margin-right: 0.5rem;
    }


    .dot.active {
    color: #00f; 
    }
